.dark-theme {
  .file-widget {
    input[type='file'] {
      &.mdl-textfield__input {
        &::-webkit-file-upload-button {
          color: $content-default-dark;
        }
      }
    }

    &.is-disabled {
      img {
        opacity: .2;
      }

      .browse-button {
        color: $primary-disabled-dark;
      }
    }
  }
}
