.dialog-container {
  z-index: $zindex-dialog-container;
  animation-name: slideInDown;
  animation-duration: 1s;
  max-width: 520px;
  background-color: $white;
  color: $black;
  box-shadow: 8px 8px 100px 0 rgba($black, .75);
  padding: 1.5em 1.5em .5em 1.5em;
  position: relative;
  border-radius: 3px;

  h1 {
    margin-top: 0;
    margin-right: 25px;
    font-size: $font-size-h4;
    text-transform: uppercase;

    img {
      vertical-align: middle;
      margin-right: .5em;
      max-height: 48px;
    }
  }

  .navigation {
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
